.member {
  padding: 120px 0 100px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(98.07deg, #0d0c11 75.45%, rgba(13, 12, 17, 0) 100%),
    rgba(1, 13, 22, 0.5);

    .container {
      @media (min-width: $screen-sm) {
        padding: 0;
      }
    }

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 850px;
    height: 660px;
    pointer-events: none;
    z-index: 1;
    background: url('/img/members/decor-1.png') 100% 100% no-repeat;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 850px;
    height: 660px;
    pointer-events: none;
    z-index: 1;
    background: url('/img/members/decor-2.png') 0 100% no-repeat;
  }

  @media (max-width: $screen-md) {
    background: transparent;
    &::before,
    &::after {
      display: none;
    }
  }

  @media (max-width: $screen-xl) {
    padding: 90px 0 90px;
  }
  @media (max-width: $screen-lg) {
    padding: 60px 0 45px;
  }
  @media (max-width: $screen-md) {
    padding: 30px 0 35px;
  }
  &__title {
    margin: 0 0 20px 0;
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 5;
  }
  &__slider-text {
    max-height: 100%;

    @media (min-width: $screen-sm) {
      max-height: 350px;
      margin: 0;
      padding: 0;
    }
    margin-right: calc($gap-sm * -1);
    margin-left: calc($gap-sm * -1);
    padding-right: calc($gap-sm);
    padding-left: calc($gap-sm);
    position: relative;
    height: 100%;
    &-body {
      padding-right: 30px;
      @media (max-width: $screen-sm) {
        padding-right: 0px;
      }
    }
    &-wrapper {
      padding-bottom: 30px;
      @media (min-width: $screen-sm) {
        flex-direction: column;
      }
    }
    &-slide {
      opacity: 0.75;
      transition: opacity $transition-time;
      &.swiper-slide-active {
        opacity: 1;
      }
      &-image {
        display: none;
        position: relative;
        padding-bottom: 80%;
        overflow: hidden;
        @media (max-width: $screen-sm) {
          display: block;
          border-radius: 20px;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
      &-title {
        font-weight: 700;
        font-size: 32px;
        margin-top: 45px;
        margin-bottom: 40px;
        @media (max-width: $screen-lg) {
          font-size: 25px;
          margin-bottom: 30px;
        }
        @media (max-width: $screen-xs) {
          font-size: 18px;
        }
        @media (max-width: $screen-sm) {
          margin: 20px 0 15px;
        }
      }
      &-description {
        font-weight: 400;
        font-size: 18px;
        line-height: 140%;
        @media (max-width: $screen-xs) {
          font-size: 16px;
        }
      }
    }
    &-controllers {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-body {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        @media (max-width: $screen-sm) {
          padding: 0 $gap-sm;
        }
        @media (max-width: $screen-xs) {
          display: none;
        }
      }
      &-scrollbar {
        width: 70%;
        height: 1px;
        background: gray;
        .swiper-scrollbar-drag {
          border-radius: 50em;
          position: relative;
          top: -1px;
          height: 3px;
          background: $color-green;
        }
      }
      &-buttons {
        display: flex;
        align-items: center;
      }
      &-button-prev {
        transform: rotate(180deg);
        margin-right: 15px;
      }
      &-button-next {
        transform: rotate(0deg);
      }
    }
  }
  &__slider-media {
    width: 50%;
    overflow: visible;
    @media (max-width: $screen-md) {
      width: 100%;
    }

    &-body {
      overflow: hidden;
      padding: 0;
      display: none;
      @media (min-width: $screen-xl) {
        margin-right: calc((100vw - $container-width-xl) / 2 * -1);
        padding-right: calc((100vw - $container-width-xl) / 2);
      }
      @media (min-width: $screen-lg) {
        margin-right: calc((100vw - $container-width-lg) / 2 * -1);
        padding-right: calc((100vw - $container-width-lg) / 2);
      }
      @media (min-width: $screen-md) {
        margin-right: calc((100vw - $container-width-md) / 2 * -1);
        padding-right: calc((100vw - $container-width-md) / 2);
      }
      @media (min-width: $screen-sm) {
        margin-right: calc((100vw - $container-width-sm) / 2 * -1);
        padding-right: calc((100vw - $container-width-sm) / 2);
      }
      @media (min-width: $screen-sm) {
        display: block;
      }
    }
    &-wrapper {
    }
    &-slide {
      opacity: 0.5;
      transition: opacity $transition-time;
      &.swiper-slide-active {
        opacity: 1;
      }
    }
    &-slide-image {
      position: relative;
      padding-bottom: 80%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
