.consultation {
  background-color: #0d0c11;
  padding: 100px 0;
  @media (max-width: $screen-xl) {
    padding: 80px 0;
  }
  @media (max-width: $screen-lg) {
    padding: 60px 0;
  }

  &__wrapper {
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 15px;
    @media (max-width: $screen-sm){
        margin-bottom: 24px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    @media (max-width: $screen-xs){
        margin-bottom: 24px;
    }
  }

  &__subtitle {
    margin: 0;
    color: rgba($color-white, 0.75);
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__button {
    display: flex;
    align-items: center;
    &:hover {
      .consultation__button-text {
        color: $color-green;
      }
    }
    &:not(:last-child) {
      margin-bottom: 10px;
      @media (max-width: $screen-xs){
        margin-bottom: 20px;
      }
    }
    &-body {
    }
    &-text {
      margin-left: 30px;
      font-weight: 600;
      font-size: 24px;
      transition: color $transition-time;
      @media (max-width: $screen-md) {
        font-size: 20px;
    }
    @media (max-width: $screen-xs){
          font-size: 16px;
          margin-left: 15px;
      }
    }
  }
}
