.button {
  flex: 0 0 auto;
    display: inline-block;
    text-align: center;
    color: #19181E;
    padding: 18px 48px;
    border-radius: 10px;
    background-color: $color-green;
    box-shadow: 0px 0px 12px rgba(232, 249, 122, 0.4);
    font-weight: 500;
    line-height: 1;
    transition: box-shadow $transition-time;
    &:hover{
        box-shadow: 0px 0px 24px rgba(232, 249, 122, 0.7);
    }

    &--size {
        &-md {
            font-size: 18px;
            @media (max-width: $screen-xs){
                font-size: 16px;
            }
        }
        &-lg {
            font-size: 20px;
            @media (max-width: $screen-xs){
                font-size: 16px;
            }
        }
    }
}

.round-button {
    display: inline-block;
    position: relative;
    z-index: 1;
    border-radius: 50%;
    background-color: rgba($color-green, 0.2);
    transition: background-color $transition-time, box-shadow $transition-time;
    &:hover {
      background-color: rgba($color-green, 0.4);
      box-shadow: 0px 0px 10px 10px rgba(192, 215, 37, 0.16);
      &::before {
        transform: scale(0.8);
      }
    }

    &--size {
        &-md {
            padding: 30px;
            svg {
                width: 20px;
                height: 20px;
            }
            @media (max-width: $screen-xs){
                  padding: 18px;
                  svg {
                    width: 13px;
                    height: 13px;
                  }
            }
        }
        &-lg {
            padding: 45px;
            svg {
                width: 32px;
                height: 32px;
            }
            @media (max-width: $screen-xs){
                  padding: 22px;
                  svg {
                    width: 16px;
                    height: 16px;
                  }
            }
        }
    }

    svg {
      color: #19181e;
      display: block;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -5;
      background-color: rgba($color-green, 0.33);
      border-radius: 50%;
      transform: scale(0.7);
      transition: transform $transition-time;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -5;
      background-color: rgba($color-green, 1);
      border-radius: 50%;
      transform: scale(0.6);
    }
}