.slider {
  overflow: hidden;
}
.swiper-wrapper {
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  display: flex;
  position: relative;
}
.slider-vertical {
  .swiper-wrapper {
    flex-direction: column;
  }
}
.swiper-autoheight {
  .swiper-wrapper {
    align-items: flex-start;
  }
}
.swiper-initialized {
  .swiper-slide {
    flex-shrink: 0;
    // transition-property: transform;
  }
}
.swiper-android {
  .swiper-slide,
  .swiper-wrapper {
    transform: translate3d(0px, 0, 0);
  }
}
.swiper-button-lock {
  display: none !important;
}

.swiper-pagination-bullet {
    cursor: pointer;
    display: inline-block;
    width: 8px;
    height: 8px;
    border: 1px solid $color-green;
    border-radius: 50%;
    transition: background-color $transition-time, transform $transition-time, box-shadow $transition-time,;
    margin: 0 15px;
}
.swiper-pagination-bullet-active {
    transform: scale(1.4);
    background-color: $color-green;
    box-shadow: 0px 0px 3px 3px rgba($color-green, 0.5);
}
.slider-button-prev, .slider-button-next {
    cursor: pointer;
    width: 24px;
    height: 24px;
    transition: color $transition-time;
    &:hover{
      color: $color-green;
    }
}
.slider-button-prev {
    transform: rotate(-90deg);
}
.slider-button-next {
    transform: rotate(90deg);
}