::-webkit-scrollbar {
  width: 14px;
  height: 10px;
  background: #0d0c11;
}
::-webkit-scrollbar-thumb {
  border: 3px solid #0d0c11;
  border-radius: 100px;
  background: $color-green;
}

::selection {
  background: $color-green;
}
::-moz-selection {
  background: $color-green; 
}
