html,
body {
  height: 100%;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: $color-white;
  @media (max-width: $screen-md){
      background: #19181E;
  }
  background: linear-gradient(90deg, #434343 0%, #111010 100%);
  
  &.lock {
    overflow: hidden;
		touch-action: none;
  }
}

* {
  outline: 0;
  font-family: inherit;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input,
textarea,
button {
  -webkit-appearance: none;
  border: none;
}

input {
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  &[type='number'] {
    -moz-appearance: textfield;
  }

  &[type='search']::-webkit-search-decoration,
  &[type='search']::-webkit-search-cancel-button,
  &[type='search']::-webkit-search-results-button,
  &[type='search']::-webkit-search-results-decoration {
    -webkit-appearance: none;
  }
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

svg {
  width: 100%;
  height: 100%;
}

ul li {
	list-style: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.responsive-video {
  position: relative;
  padding: 56.25% 0 0;

  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
	overflow: hidden;

  &__main {
    margin: 0 0 auto;
  }
}

p {
  margin: 0 0 1em;

  &.reset-m {
    margin: 0;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 1.3;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.2em 0 0.6em;

  &.reset {
    margin: 0;

    &-mt {
      margin-top: 0;
    }

    &-mb {
      margin-bottom: 0;
    }
  }
}

h1,
.h1 {
  font-size: 48px;
  font-weight: 700;
  line-height: 1.6em;
  text-transform: uppercase;
  margin-top: 0;

  @media (max-width: $screen-lg) {
    font-size: 32px;
  }
}
h2,
.h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 1.4em;
  text-transform: uppercase;

  @media (max-width: $screen-lg){
    font-size: 28px;
  }
}
h3,
.h3 {
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;

  @media (max-width: $screen-lg){
    font-size: 20px;
  }
}
h4,
.h4 {
  font-size: 19px;
}
h5,
.h5 {
  font-size: 17px;
}
h6,
.h6 {
  font-size: 15px;
}
