.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(#0D0C11, .5);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: opacity $transition-time;
    z-index: 900;
}
.popup.open {
	opacity: 1;
	visibility: visible;
}
.popup.open .popup__content {
	opacity: 1;
}

.popup__wrapper {
	min-height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 30px 12px;
    transition: visibility $transition-time;
}
.popup__content {
	background-color: $color-white;
	color: #000;
	max-width: 510px;
	padding: 40px;
	position: relative;
	transition: opacity $transition-time;
	opacity: 0;
    border-radius: 10px;
    @media (max-width: $screen-xs){
        padding: 40px 25px;
    }
}
.popup__close {
	position: absolute;
	right: 0;
	top: 0;
    transform: translate(100%, -100%);
    width: 28px;
    height: 28px;
    border-radius: 100px;
    color: $color-white;
    background-color: rgba($color-white, .33);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    svg {
        width: 18px;
        height: 18px;
    }
    transition: background-color $transition-time;
    &:hover{
        background-color: rgba($color-white, .5);
    }
    @media (max-width: $screen-sm){
        transform: translate(0%, 0%);
        right: 10px;
        top: 10px;
        background-color: rgba($color-black, .33);
        &:hover{
            background-color: rgba($color-black, .5);
        }
    }
}