.activity {
  padding: 120px 0;

  @media (max-width: $screen-xl) {
    padding: 90px 0 90px;
  }
  @media (max-width: $screen-lg) {
      padding: 60px 0 45px;
    }
    @media (max-width: $screen-md) {
        padding: 60px 0 35px;
    }
  .container {
    position: relative;
    @media (min-width: $screen-sm) {
      padding: 0;
    }
    &::before {
      content: '';
      position: absolute;
      width: 34.375em;
      height: 40.625em;
      left: 0;
      bottom: 0;
      background: $color-green;
      opacity: 0.2;
      z-index: -1;
      filter: blur(200px);
      transform: translate(-75%, 15%) rotate(75deg) matrix(1, 0, 0.05, 1, 0, 0);
    }
    &::after {
      content: '';
      position: absolute;
      width: 31.25em;
      height: 40.625em;
      top: 0;
      right: 0;
      background: rgba($color-green, 0.8);
      opacity: 0.2;
      filter: blur(200px);
      transform: translate(50%, -60%) rotate(75deg) matrix(0.26, 0.97, -0.97, 0.26, 0, 0);
      z-index: -1;
      @media (max-width: $screen-md) {
        font-size: 0.75em;
        transform: translate(33%, -35%) rotate(75deg) matrix(0.26, 0.97, -0.97, 0.26, 0, 0);
      }
    }
  }
  &__title {
    margin: 0 0 40px 0;

    @media (max-width: $screen-md) {
      margin: 0 0 25px 0;
    }
  }
  &__wrapper {
    position: relative;
  }
  &__body {
    padding-right: 65px;

    @media (max-width: $screen-md) {
      padding-right: 0px;
    }
  }
  &__text-header {
    &-block {
      color: $color-green;
      display: flex;
      margin-bottom: 35px;

      @media (max-width: $screen-md) {
        align-items: center;
        margin-bottom: 16px;
      }
    }
    &-title {
      font-weight: 800;
      font-size: 72px;
      line-height: 100%;
      text-transform: uppercase;
      margin-right: 35px;

      @media (max-width: $screen-md) {
        font-size: 36px;
        margin-right: 15px;
      }
    }
    &-subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 140%;

      @media (max-width: $screen-md) {
        font-size: 20px;
        br {
          display: none;
        }
      }
    }
    &-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      @media (max-width: $screen-xl) {
        font-size: 13px;
      }

      @media (max-width: $screen-md) {
        margin-bottom: 35px;
      }
    }
    &-item {
      &.list__item:not(:last-child),
      &.list__item {
        margin-bottom: 10px;
        flex: 0 0 auto;
        color: rgba($color-white, 0.75);
        margin-right: 15px;
      }
      .list__item-icon {
        @media (max-width: $screen-md) {
          box-shadow: none;
        }
      }
      .list__item-content {
      }
    }
  }
  &__accordion {
    min-height: 400px;
    &-item {
      border-top: 1px solid rgba($color-white, 0.5);
      &:last-child {
        border-bottom: 1px solid rgba($color-white, 0.5);
      }
    }
    &-title {
      padding: 33px 0;
      font-weight: 500;
      font-size: 18px;
      line-height: 140%;
      text-transform: uppercase;
      transition: color $transition-time;
      cursor: pointer;
      @media (max-width: $screen-md) {
        padding: 28px 0;
      }
      @media (any-hover: hover) {
        &:hover {
            color: rgba($color-green, 0.5);
        }
      }
      .active & {
        color: $color-green;
        cursor: default;
        &:hover {
          color: $color-green;
        }
      }
    }
    &-wrapper {
      display: none;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      max-height: 750px;
      .active & {
        display: block;
      }
      @media (max-width: $screen-md) {
        position: static;
      }
      &-body {
      }
      &-slider {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        
        @media (min-width: $screen-xl) {
          margin-right: calc((100vw - $container-width-xl) / 2 * -1);
          padding-right: calc((100vw - $container-width-xl) / 2);
        }
        @media (min-width: $screen-lg) {
          margin-right: calc((100vw - $container-width-lg) / 2 * -1);
          padding-right: calc((100vw - $container-width-lg) / 2);
        }
        @media (min-width: $screen-md) {
          margin-right: calc((100vw - $container-width-md) / 2 * -1);
          padding-right: calc((100vw - $container-width-md) / 2);
          margin-left: 0;
          padding-left: 0;
        }
        @media (min-width: $screen-sm) {
          margin-right: calc((100vw - $container-width-sm) / 2 * -1);
          margin-left: calc((100vw - $container-width-sm) / 2 * -1);
          padding-right: calc((100vw - $container-width-sm) / 2);
          padding-left: calc((100vw - $container-width-sm) / 2);
        }
        margin-right: calc($gap-sm * -1);
        margin-left: calc($gap-sm * -1);
        padding-right: calc($gap-sm);
        padding-left: calc($gap-sm);

        &-wrapper.swiper-wrapper {
          height: 90%;

          @media (max-width: $screen-md) {
            margin-bottom: 30px;
          }
        }
        &-slide {
          border-radius: 10px;
          position: relative;
          overflow: hidden;
          opacity: 0.75;
          transition: opacity $transition-time;
          &.swiper-slide-active {
            opacity: 1;
          }

          @media (max-width: $screen-md) {
            padding-bottom: 70%;
          }
          &-body {
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              position: absolute;
              top: 0;
              left: 0;
            }
          }
        }
      }
      &-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @media (max-width: $screen-md) {
          margin: 20px 0;
        }
        @media (max-width: $screen-sm) {
          display: none;
        }
        &-controllers {
          display: flex;
          align-items: center;
          &-prev {
          }
          &-padination {
          }
          &-next {
          }
        }
        &-link {
          font-size: 18px;
          line-height: 100%;
          display: flex;
          align-items: center;
          transition: color $transition-time;
          &:hover {
            color: $color-green;
          }
          svg {
            margin-left: 0.444em;
            width: 1.333em;
            height: 1.333em;
            transform: rotate(-90deg);
          }
        }
      }
    }
  }
}
