.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 800;
  padding-top: 35px;
  padding-bottom: 35px;
  transition: padding-top $transition-time, padding-bottom $transition-time, background-color $transition-time;
  @media (max-width: $screen-md) {
    padding-top: 25px;
    padding-bottom: 25px;
  }
  @media (max-width: $screen-sm) {
    padding-top: 12px;
    padding-bottom: 12px;
  }

  &.scrolling {
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #0d0c11;
    @media (max-width: $screen-md) {
      padding-top: 15px;
      padding-bottom: 15px;
    }
    @media (max-width: $screen-sm) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }
  .header__body {
  }
  .header__logo {
    max-height: 90px;
    @media (max-width: $screen-lg) {
      max-height: 70px;
    }
    @media (max-width: $screen-md) {
      max-height: 60px;
    }
    @media (max-width: $screen-xs) {
      max-height: 36px;
    }
  }
  .header__items {
    display: flex;
    align-items: center;
  }
  .header__item {
    display: flex;
    align-items: center;
    margin-left: 8px;
    @media (max-width: $screen-md) {
      &.item--text {
        display: none;
      }
    }
  }
  .header__devider {
    width: 100px;
    height: 1px;
    background-color: rgba($color-white, 0.25);
    @media (max-width: $screen-xl) {
        width: 90px;
    }
    @media (max-width: $screen-lg) {
      width: 50px;
    }
    @media (max-width: 1100px) {
      width: 25px;
    }
    @media (max-width: $screen-md) {
      &.devider--1,
      &.devider--4 {
        width: 0;
      }
    }
    @media (max-width: $screen-sm) {
      width: 8px;
    }
    @media (max-width: $screen-xs) {
      width: 0;
    }
  }
  .header__content {
    display: flex;
    align-items: center;
    margin-left: 8px;
    color: $color-white;

    & .icon {
      font-size: 1em;
      width: 2.75em;
      height: 2.75em;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba($color-white, .1);
      transition: background-color $transition-time;
      &:hover{
        background-color: rgba($color-green, .7);
      }
      @media (max-width: $screen-md) {
        font-size: 0.8em;
      }
      @media (max-width: $screen-xs) {
        font-size: 0.65em;
      }

      svg {
        color: rgba($color-white, 0.75);
      }

      &--vk {
        svg {
          font-size: 1em;
          width: 1.25em;
          height: 0.75em;
        }
      }

      &--tg {
        svg {
          font-size: 1em;
          width: 1.375em;
          height: 1.125em;
          margin-top: 0.125em;
          margin-right: 0.125em;
        }
      }

      &--mail {
        svg {
          font-size: 1em;
          width: 1.5625em;
          height: 1.125em;
        }
      }

      &--pin {
        width: auto;
        height: auto;
        background-color: transparent;
        svg {
          font-size: 1em;
          width: 1.875em;
          height: 2.5em;
          margin-right: 0.75em;
          margin-left: 0.4375em;
        }
        &:hover{
          background-color: transparent;
        }
      }
    }
  }
}
