.footer {
  padding: 120px 0;
  background: #0d0c11;
  position: relative;

  @media (max-width: $screen-lg) {
    padding: 60px 0 45px;
  }
  @media (max-width: $screen-md) {
    background: transparent;
    padding: 30px 0 35px;
  }
  .row {
    flex-direction: column;
    text-align: center;
  }
  &__header {
    margin: 0 0 75px 0;
    @media (max-width: $screen-lg){
        margin: 0 0 28px 0;
    }
    &-title {
      margin: 0 0 20px 0;
      text-transform: none;
      @media (max-width: $screen-lg){
          font-size: 20px;
          margin: 0 0 40px 0;
        }
    }
    &-subtitle {
        margin: 0px;
        font-weight: 500;
        @media (max-width: $screen-lg){
            font-size: 16px;
      }
    }
  }
  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 60px;
    @media (max-width: $screen-lg){
        margin-bottom: 30px;
    }
    &-title {
      margin: 0;
      font-weight: 500;
      font-size: 40px;
      line-height: 140%;
      @media (max-width: $screen-lg){
          font-size: 24px;
          margin: 0 0 7px 0;
          line-height: 140%;
      }
    }
    &-subtitle {
      font-weight: 500;
      font-size: 24px;
      line-height: 200%;
      margin: 20px 0;
      @media (max-width: $screen-lg){
          font-size: 16px;
          margin: 0 0 35px 0;
      }
    }
    &-contacts {
      display: flex;
      align-items: center;
      &-button {
        width: 60px;
        height: 60px;
        display: block;
        &:hover{
            svg {
                color: $color-green;
            }
        }
        svg {
            transition: color $transition-time;
        }
      }
      &-menager {
        display: block;
        width: 100px;
        height: 100px;
        margin: 0 25px;
      }
    }
  }
  &__footer {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 50px;
    @media (max-width: $screen-sm){
        flex-direction: column;
        align-items: center;
        margin-bottom: 26px;
    }
    &-logo {
      display: flex;
      max-height: 90px;
      padding: 0 7px 0 0;
      @media (max-width: $screen-sm){
        display: none;
      }
    }
    &-info {
      font-weight: 400;
      font-size: 18px;
      line-height: 140%;
      padding: 0 7px 0 7px;
      @media (max-width: $screen-lg){
          font-size: 16px;
      }
    }
    &-phone {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-weight: 600;
      font-size: 24px;
      line-height: 100%;
      opacity: 0.75;
      padding: 0 0 0 7px;
      transition: color $transition-time;
      &:hover{
        color: $color-green;
      }
      @media (max-width: $screen-lg){
          font-size: 16px;
      }
      @media (max-width: $screen-sm){
          justify-content: center;
          margin-top: 20px;
      }
      svg {
        width: 75px;
        height: 73px;
        margin-right: 25px;
        @media (max-width: $screen-lg){
            margin-right: 10px;
            width: 50px;
            height: 50px;
        }
      }
    }
  }
  &__copyright {
    font-weight: 700;
    font-size: 14px;
    line-height: 140%;
    @media (max-width: $screen-lg){
        font-weight: 300;
    }
  }
  &__decor {
    pointer-events: none;
    &.decor_1 {
        position: absolute;
        top: 0;
        right: 0;
    }
    &.decor_2 {
        display: none;
    }
    @media (max-width: $screen-md){
        &.decor_1 {
            display: none;
        }
        &.decor_2 {
            display: block;
            position: absolute;
            top: 100px;
            right: 0;
        }
    }
  }
}
