#form.popup {
    .popup__wrapper {
    }
    .popup__content {
    }
    .popup__close {
    }
    .popup__body {
    }
    .popup__titles {
    }
    .popup__title,
    .popup__subtitle {
      font-weight: 700;
      font-size: 20px;
      color: #0d0c11;
      text-transform: uppercase;
    }
    .popup__title {
      margin: 0 0 12px 0;
    }
    .popup__subtitle {
      margin: 0;
      color: #c0d725;
    }
    .popup__inputs {
        padding: 35px 0 50px;
        @media (max-width: $screen-xs){
            padding: 10px 0 50px;
        }
    }
    .popup__input {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    .popup__button {
        width: 100%;
    }
  }
