.member-for {
    padding: 120px 0 100px;
    background: #19181E;
    position: relative;
    @media (max-width: $screen-md){
        background: transparent;
    }
    
    @media (max-width: $screen-xl){
        padding: 90px 0 90px;
    }
    @media (max-width: $screen-lg){
        padding: 60px 0 45px;
    }
    @media (max-width: $screen-md) {
        padding: 60px 0 35px;
    }
    .container {
    }

    &__text {}

    &__title {
        margin: 0;
        color: $color-green;
    }
    &__subtitle {
        margin: 20px 0 40px;
        
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
    }
    &__item {
        &:nth-child(odd) {
            padding-right: 15px;
        }
    }
    &__decor {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 5vw;
        img {
            height: 100%;
        }
        @media (max-width: $screen-md){
            top: 30%;
            right: -100px;
            bottom: auto;
            transform: rotate(-74.21deg);
            max-width: 365px;
            img {
                height: 100%;
            }
        }
    }
}