.first-screen {
  background-color: #19181e;
  padding-top: 260px;
  position: relative;
  @media (max-width: $screen-xl) {
    padding-top: 200px;
  }
  @media (max-width: $screen-md) {
    padding-top: 150px;
  }
  @media (max-width: $screen-xs) {
    padding-top: 70px;
  }
  .container {
  }
  &__header {
    flex-wrap: nowrap;
    position: relative;
    z-index: 5;
    &-text {
      align-self: center;
      max-width: 660px;
      padding-bottom: 90px;
      
      &-title {
        margin-bottom: 180px;
        span {
          color: $color-green;
        }
        @media (max-width: $screen-md) {
          margin-bottom: 20px;
        }
      }
      @media (max-width: $screen-md) {
        padding-bottom: 0px;
      }
    }
    &-button {
      @media (max-width: $screen-xs){
        width: 100%;
      }
    }
    @media (max-width: $screen-md) {
      flex-direction: column;
      align-items: center;
    }
    &-call-button {
      line-height: 0;
      margin-bottom: 70px;

      @media (max-width: $screen-md) {
        order: 3;
        display: flex;
        margin: 40px auto;
      }
      @media (max-width: $screen-xs) {
        margin: 20px auto;
      }

      &-decor {
        &-devider {
          width: 1px;
          height: 360px;
          margin: 0 auto;
          background: rgba($color-white, 0.4);
        }
        &-arrow {
          margin: 10px auto 20px;
          width: 28px;
          height: 28px;
        }
        @media (max-width: $screen-lg) {
          margin-bottom: 0px;
        }
        @media (max-width: $screen-md) {
          display: flex;
          &-devider {
            width: 50px;
            height: 1px;
            margin: auto 0;
          }
          &-arrow {
            margin: auto 10px auto auto;
            transform: rotate(-90deg);
          }
        }
        &:last-child {
          display: none;
          @media (max-width: $screen-md) {
            display: flex;
            .first-screen__header-call-button-decor-arrow {
              margin: auto auto auto 10px;
              transform: rotate(90deg);
            }
          }
        }
      }
      &-body {
      }
    }
    &-media {
      &-title {
        font-weight: 500;
        font-size: 28px;
        margin-bottom: 70px;
        color: rgba(255, 255, 255, 0.5);

        @media (max-width: $screen-lg) {
          font-size: 20px;
          margin-bottom: 20px;
        }
        @media (max-width: $screen-md) {
          text-align: center;
        }
        @media (max-width: $screen-xs) {
          text-align: start;
        }
      }
      @media (max-width: $screen-md) {
        max-width: 100%;
      }

      &-slider {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-body {
          width: 100%;
          height: 460px;
          width: 460px;
          @media (max-width: $screen-xl) {
            max-height: 400px;
            max-width: 400px;
          }
          @media (max-width: $screen-lg) {
            height: 350px;
            width: 350px;
          }
          @media (max-width: $screen-md) {
            height: 100%;
            width: 100%;
            max-height: 500px;
            max-width: 500px;
            overflow: visible;
          }
          @media (max-width: $screen-xs) {
            overflow: visible;
          }
        }
        &-wrapper {
          flex-direction: column;
          @media (max-width: $screen-md){
            flex-direction: row;
          }
        }

        &-slide {
          border-radius: 10px;
          overflow: hidden;
          position: relative;
          padding: 0 0 100% 0;
          opacity: 0.75;
          transition: opacity $transition-time;
          &.swiper-slide-active {
            opacity: 1;
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            position: absolute;
            top: 0;
            left: 0;
          }
        }

        &-controllers {
          margin-left: 100px;
          @media (max-width: $screen-xl) {
            margin-left: 30px;
          }
          @media (max-width: $screen-md) {
            display: none;
          }
        }

        &-button {
          &-prev {
            transform: rotate(0);
          }
          &-next {
            transform: rotate(180deg);
          }
        }

        &-padination {
          .swiper-pagination-bullet {
            display: block;
            margin: 15px auto;
          }
        }
      }
    }
    &-button {
      position: absolute;
      bottom: 45%;
      @media (max-width: $screen-md){
        position: static;
        margin-top: 20px;
      }
    }
  }
  &__footer {
    padding-top: 80px;
    padding-bottom: 120px;
    position: relative;
    z-index: 5;

    @media (max-width: $screen-xl) {
      justify-content: space-between;
    }
    @media (max-width: $screen-lg) {
      padding-top: 50px;
      padding-bottom: 80px;
    }
    @media (max-width: $screen-md) {
      flex-direction: column;
      align-items: center;
    }
    @media (max-width: $screen-xs) {
      align-items: flex-start;
      padding-bottom: 60px;
    }

    &-body {
    }

    &-column {
      padding-right: 20px;
      &:last-child {
        padding-right: 0;

        @media (max-width: $screen-lg) {
          display: flex;
          width: 100%;
          justify-content: space-between;
          padding-right: 20px;
          margin-top: 50px;
        }
        @media (max-width: $screen-xs) {
          margin-top: 20px;
        }
      }
      @media (max-width: $screen-md) {
        &:first-child {
          margin-bottom: 50px;
        }
        &:last-child {
          display: block;
          width: auto;
        }
      }
      @media (max-width: $screen-xs) {
        &:first-child {
          margin-bottom: 20px;
        }
      }
    }

    &-item {
      display: flex;
      transition: color $transition-time;

      @media (max-width: $screen-xs) {
        flex-direction: column;
      }

      &:first-child {
        margin-bottom: 80px;

        @media (max-width: $screen-lg) {
          margin-bottom: 50px;
        }
        @media (max-width: $screen-xs) {
          margin-bottom: 20px;
        }
      }

      &:hover {
        color: $color-green;
      }

      &-number {
        font-weight: 700;
        font-size: 80px;
        margin-right: 12px;
        line-height: 1em;
        span {
          font-size: 28px;
        }
        @media (max-width: $screen-xl) {
          font-size: 70px;
        }
        @media (max-width: $screen-xs) {
          font-size: 40px;
          margin-bottom: 10px;
        }
      }

      &-info {
        font-weight: 400;
        font-size: 18px;
        line-height: 1.4em;
        opacity: 0.75;
        max-width: 250px;
        @media (max-width: $screen-xs) {
          font-size: 16px;
        }
        @media (max-width: $screen-xs) {
          max-width: 100%;
        }
      }
    }
  }
  &__decors {
  }
  &__decor {
    &.decor_1 {
      position: absolute;
      top: 0;
      left: 0;
      max-width: 80vw;
    }
    &.decor_2 {
      position: absolute;
      top: 0;
      right: 0;
      max-width: 80vw;
      opacity: 0.1;
    }
    &.decor_3 {
      position: absolute;
      bottom: 0;
      left: 10vw;
      width: 30vw;
      opacity: 0.4;
      @media (max-width: $screen-md) {
        opacity: 1;
        left: auto;
        width: 100vw;
        right: -30vw;
      }
    }
  }
}
