.join {
  padding: 120px 0 100px;
  background: #19181e;
  position: relative;
  @media (max-width: $screen-md) {
    background: transparent;
  }

  @media (max-width: $screen-xl) {
    padding: 90px 0 90px;
  }
  @media (max-width: $screen-lg) {
    padding: 60px 0 45px;
  }
  @media (max-width: $screen-md) {
      padding: 0 0 35px;
  }
  .container {
  }

  &__text {
  }

  &__title {
    margin: 0;
    color: $color-green;
    position: relative;
    z-index: 5;
    margin-bottom: 50px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 5;
  }
  &__item {
    &:nth-child(odd) {
      padding-right: 15px;
    }
  }
  &__do {
    margin-top: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 5;

    @media (max-width: $screen-xl) {
      margin-top: 80px;
    }
    @media (max-width: $screen-lg) {
      margin-top: 50px;
    }
    @media (max-width: $screen-md) {
        flex-direction: column;
        align-items: flex-start;
    }
    @media (max-width: $screen-xs) {
        align-items: stretch;
    }

    &-title {
      font-weight: 700;
      font-size: 45px;
      line-height: 1.4;
      text-transform: uppercase;
      color: $color-green;
      @media (max-width: $screen-lg) {
          font-size: 28px;
        }
        @media (max-width: $screen-lg) {
            font-size: 25px;
        }
    }
    &-button {
      @media (max-width: $screen-md) {
        margin: 30px 0 0;
      }
    }
  }
  &__decor {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    img {
      height: 100%;
    }
    .decor_2 {
      display: none;
    }
    @media (max-width: $screen-md) {
      .decor_1 {
        display: none;
      }
      .decor_2 {
        display: block;
      }
    }
    @media (max-width: $screen-md) {
      top: 30%;
      right: -100px;
      bottom: auto;
      transform: rotate(-74.21deg);
      max-width: 365px;
      img {
        height: 100%;
      }
    }
  }
}
