.list {
    &--style {
        &-check {
            .list__item {
                &-icon {
                    
                    position: relative;
                    &::before{
                        content:'';
                        position: absolute;
                        top: 0;
                        left: 0;
                        bottom: 0;
                        right: 0;
                        border-radius: 50%;
                        background-color: rgba($color-green, 1);
                        transform: scale(.8);
                    }
        
                    svg {
                        width: 0.778em;
                        height: 0.778em;
                        color: #19181E;
                        position: relative;
                        z-index: 5;
                    }
                }
            }
        }
        &-dot {
            .list__item {
                &-icon {
                    margin-top: 0.222em;
                    flex: 0 0 auto;
                    padding: 0;
                    width: 0.666em;
                    height: 0.666em;
                    box-shadow: 0px 0px 12px 4px rgba(192, 215, 37, 0.5);
                    background-color: $color-green;
                    &::before{
                        display: none;
                    }
                }
                &-content {
                    line-height: 1;
                }
            }
        }
    }
    &__item {
        font-size: 1.125em;
        display: flex;
        align-items: flex-start;
        
        &-icon {
            padding: 0.278em;
            display: inline-flex;
            background-color: rgba($color-green, .5);
            border-radius: 50%;
        }
        
        &:not(:last-child) {
            margin-bottom: 1.388em;
        }
        
        &-content {
            margin-left: 0.667em;
        }
    }
}