input {
    display: block;
    width: 100%;
    padding: 12px 0;
    font-weight: 300;
    font-size: 16px;
    color: #0F0C3A;
    border-bottom: 1px solid rgba(15, 12, 58, 0.2);
    transition: border-bottom $transition-time;
    &:focus {
        border-bottom: 1px solid rgba(15, 12, 58, 0.5);
    }
}